body {
  object-fit: none;
  background-image: url("https://img.freepik.com/free-photo/casino-roulette-wheel-close-up-ai-generative_123827-24159.jpg?w=2000&t=st=1693407918~exp=1693408518~hmac=8e9388df6c54bf3807c51d05220dc27607c1deffc326b03b7322d20e750dab56");
}

.wrapper {
  width: 1100px;
  gap: 24px;
  padding: 12px;
  display: flex;
}

.item {
  width: 384px;
  height: 384px;
  text-align: center;
  color: #f0f8ff;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #fff;
  align-items: center;
}

.container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 24px;
  display: flex;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.btn {
  width: 300px;
  height: 60px;
  color: #f0f8ff;
  background-color: #00bfff;
  font-size: 36px;
}

.btn:hover {
  background-color: #add8e6;
}

.span {
  opacity: 0;
  margin-top: 300px;
  font-size: 0;
  transition: all .5s linear;
}

.appears {
  opacity: 1;
  font-size: 328px;
}

.text_won {
  width: 700px;
  color: #f0f8ff;
  opacity: 0;
  margin-top: 0;
  margin-left: 0;
  margin-right: 0;
  font-size: 0;
  transition: all .5s linear;
  position: absolute;
  top: 15%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.text_loose {
  width: 750px;
  color: #f0f8ff;
  opacity: 0;
  margin-top: 0;
  margin-left: 0;
  margin-right: 0;
  font-size: 0;
  transition: all .5s linear;
  position: absolute;
  top: 15%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.text_apear {
  opacity: 1;
  font-size: 148px;
}

.cross {
  color: green;
}

.red {
  color: red;
}

